// import Bmob from '@/utils/bmob.js'
import postCard from '@/components/postCard';
export default {
	components: {
		postCard
	},
	data() {
		return {
			value: '',	// 搜索内容
			pageSetting: {
				pageIndex: 1,	// 页码
				pageSize: 10,	// 每页页数
				totalPage: 1,	// 总页数
				totalSize: 0,	// 总条数数
			},
			bannerList: [],	// 菜单分类列表
			postClassList: [],	// 帖子分类
			postList: [],	// 帖子列表
			pickIndex: 0,
			pickList: [{name: '不限'}],
			localId: '',	// 地址的id， 默认为空
		}
	},
	computed: {
	},
	async onLoad() {
		let that = this;
		//	获取地址
		uni.getStorage({
		    key: 'localId',
		    success: function (res) {
				that.localId = res.data ? res.data : ''; 
				that.getLocalList();
				that.getBannerList();
				that.getPostClassList();
				that.getPostList();
		    }
		});
	},
	onReady(){
	},
	onShow(){
		// this.getPostList();
	},
	// 分享
	onShareAppMessage() {
		return {
			title: '邀请您使用小镇事事通',
			path: '/pages/index/index'
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.pageSetting.pageIndex = 1;
		this.postList = [];
		this.getPostList();
		this.getLocalList();
		this.getBannerList();
		this.getPostClassList();
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	// 到底
	onReachBottom(){
		if (this.pageSetting.pageIndex <= this.pageSetting.totalPage) {
			//设置列表数据
			this.getPostList()
		}
	},
	methods: {
		// 详情、结果
		navTo(url){
			uni.navigateTo({ url: url })
		},
		// 获取地址列表
		getLocalList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('local');
			// 查询所有数据
			query.find().then(res => {
			  uni.hideLoading();
			  that.pickList = that.pickList.concat(res);
			  that.pickList.forEach( (v, i) => {
				  if(v.objectId === that.localId){
					that.pickIndex = i
				  }
			  })
			});
		},
		// 获取广告图片
		getBannerList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('banner');
			query.order("-createdAt");
			query.limit(5);
			// 查询所有数据
			query.find().then(res => {
			  uni.hideLoading();
			  that.bannerList = res;
			});
		},
		// 获取帖子分类
		getPostClassList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('postClass');
			query.limit(5);
			// 查询所有数据
			query.find().then(res => {
			  uni.hideLoading();
			  that.postClassList = res;
			});
		},
		// 获取帖子列表
		getPostList() {
			let that = this;
			uni.showLoading({
				title: '加载中'
			});
			var query = that.Bmob.Query('postList');
			query.limit(10);	// 每页条数
			query.skip(10 * (that.pageSetting.pageIndex - 1));	// 分页查询// 对score字段降序排列
			query.order("-createdAt");
			query.include("author,belongsClass", "_User,postClass");
			// 如果选择了地址，则关联地址
			if(that.localId){
				query.equalTo("belongsLocal", "==", that.localId);
			}
			
			query.count().then(res => {
				if(res.count === 0){
					that.postList = []
					uni.hideLoading();
				}else{
					that.pageSetting.totalPage = parseInt(res.count/that.pageSetting.pageSize) + 1
					query.find().then(res => {
						uni.hideLoading();
						that.pageSetting.pageIndex += 1;
						that.postList = that.postList.concat(res);
					});
				}
			});
		},
		// 删除帖子
		deletePost(index){
			this.postList.splice( index, 1)
		},
		// 切换pick地址选择
		bindPickerChange: function(e) {
			let that = this;
			this.pickIndex = e.target.value
			this.localId = this.pickList[parseInt(this.pickIndex)].objectId ? this.pickList[parseInt(this.pickIndex)].objectId : ''
			//更新地址
			uni.setStorage({
			  key: 'localId',
			  data: that.localId,
			  success: function () {
				  console.log('更新地址成功')
			  }
			});
			that.postList = [];
			that.pageSetting.pageIndex = 1;
			that.getPostList()
		},
	}
}
